.ud-random-img-block {
	&-pro {
		&.ud-radmon-img-block-caption-over-img {
			.ud-random-img-block__content {
				position: absolute;
				top: var( --ud-rib-overlay-position-y, 50% );
				left: var( --ud-rib-overlay-position-x, 50% );
				z-index: 999;
				transform: translate( var( --ud-rib-overlay-position-t-y, -50% ), var( --ud-rib-overlay-position-t-x, -50% ) );
			}
		}
	}
	&-caption-on-hover {
		transition: all 0.3s ease-in-out;
		.ud-random-img-block__content-caption {
			opacity: 0;
			visibility: hidden;
			transition: all 0.3s ease-in-out;
		}
		&:hover {
			.ud-random-img-block__content-caption {
				opacity: 1;
				visibility: visible;
			}
		}
	}
	&__images {
		position: relative;
		img {
			max-width: 100%;
			max-height: 100%;
		}
	}
	&__content {
		figcaption {
			font-size: inherit;
			text-align: inherit !important;
		}
	}
}
